.footer__logo {
  display: none;

  @media #{$desktop} {
    @include hidden_text;
    display: block;
    background: url('../img/vinciworks.svg') no-repeat;
    background-size: contain;
    background-position: 100% 50%;
    height: $footer_height/3;
    margin: $footer_height/3 1rem;
    position: absolute;
    bottom: 0;
    width: $footer_height/3*4.64;
    right: 0;
    z-index: 2;
    opacity: 0.6;
    transition: opacity 0.4s;

    &:hover {
      opacity: 1;
    }
  }
}
