.welcome-button {
  text-align: center;

  &__button {
    @extend %button;
    margin: leading(2) -1rem;
    background-color: $course_white;
    padding: 0 2rem;
    width: calc(100% + 1rem);
    max-width: 8em;
    line-height: leading(3);
    display: inline-flex;
    justify-content: center;
    border-radius: 100vw;
    text-shadow: none;
    color: $course_black;
    font-size: 2.4rem;
    border: none;
    box-shadow: none;
    text-align: center;
    white-space: nowrap;
    cursor:pointer;
    &:hover {
      background-color: lighten($course_white, 10%);
    }
    &:active {
      transform: none;
    }
  }

  &__button-text {
    display: inline-block;
    text-align: center;
    line-height: leading(3);
    @media (max-width: 50em) {
      font-size: 1.6rem;
      line-height: leading(3);
    }
  }

  &__arrow {
    display: inline-block;
    position: absolute;
    right:leading(1);
    top: leading(1.5)/2;
    height: leading(1.5);
    @media (max-width: 20em) {
      transform: scale(.7);
    }
    @media (max-width: 15em) {
      display: none;
    }
  }

  &__note {
    text-align: center;
    font-style: italic
  }
}
