// ====================================
//
//   _CONFIG.SCSS
//
// ====================================


// ====================================
//   Color Palette
// ====================================

// Check out http://chir.ag/projects/name-that-color/
// for help naming colors. 

$black					: #000000;
$white					: #FFFFFF;
$deep_blue			: #1f0b7a;
$san_marino			: #414ab2;
$blue_zodiac		: #12264f;
$razzmatazz			: #e30053;

// Sometimes a component needs a black, white, or primary

$course_black   : black;
$course_white   : white;
$course_primary : steelblue;
$course_bad     : deeppink;
$course_good    : springgreen;
$course_neutral : lightgray;
$background_gradient : linear-gradient(to top, #fff2fe, #f0efff);

// ====================================
//   Font Families
// ====================================

@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:200,200i,300,300i,400,400i,600,600i,700,700i,900,900i'); //sass-lint: discard space-after-comma

$font_family_primary   : 'Source Sans Pro', 'Helvetica Neue', Helvetica, Arial, sans-serif;
$font_family_secondary : 'Source Sans Pro', Helvetica, Arial, sans-serif;


// ====================================
//   Font Sizes
// ====================================

// Based on Gutenberg by Matej Laten
// https://github.com/matejlatin/Gutenberg

$base_font_size : 16px;
$leading : 1.5;

// Headings Options
// [ h1: size (in rem), line height (* leading), margin top (* leading), margin bottom (* leading) ]
$headings: (
  h1 : ( 2.5,    2,   4,   1 ),
  h2 : ( 1.6875, 1.5, 2.5, 1 ),
  h3 : ( 1.375,  1,   2,   1 ),
  h4 : ( 1.2,    1,   1.5, 1 ),
  h5 : ( 1,      1,   2.5, 1 ),
  h6 : ( 1,      1,   2.5, 1 )
) !default;


// ====================================
//   Layout Dimensions
// ====================================

$menu_height   : 2rem * $leading;
$menu_padding  : 0.5rem * $leading;
$arrow_height  : 5rem * $leading;
$arrow_width   : 4rem;
$footer_height : $menu_height + $menu_padding * 2;


// ====================================
//   Border Radius
// ====================================

$border_radius : 3px;


// ====================================
//   Transitions
// ====================================

$tr_dur : 200ms;
$page_tr_dur : 200ms;


// ====================================
//   Breakpoints
// ====================================

$desktop       : '(min-width: 50em)';
$desktop_large : '(min-width: 75em)';
$phone         : '(max-width: 48em)';
