// ====================================
//  Buttons
// ====================================

// container for a row of buttons
%buttons, .buttons {
  text-align: center;
  user-select: none;
}

// standard button
// NOTE: this is deprecated, use the mixin
%button, .button {
  @include button;
  &.help_button {
  	background-color: $razzmatazz;
  }
}

.button-menu {
	display: flex;
	flex-direction: column;
	.button {
		flex: 1;
	}
}

