.arrows {
  @include clearfix;
  position: absolute;
  bottom: 0;
  width: 100%;
  z-index: 2;
  user-select: none;
  display: block;


  &__arrow--prev-hint,
  &__arrow--next-hint {
    @include peripheral_hint;
    display: none;
    position: fixed;
    top: 50%;
    transform: translateY(-50%);
    left: $arrow_width;
    // width: 8em;
    line-height: 1.2;

    @media (min-width: 70em) {
      display: block;
    }

    .-show-hints & {
      @include visible;
    }
  }

  &__arrow--next-hint {
    left: auto;
    right: $arrow_width;
    text-align: right;
  }
  
  &__arrow--prev,
  &__arrow--next {
    @include flex_parent_centered;
    color: rgba($white, 1);
    height: $arrow_height;
    width: $arrow_width;
    overflow: hidden;
    cursor: pointer;
    font-size: 2em;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    bottom: 0;

    @media #{$desktop} {
      top: 50%;
      transform: translateY(-50%);
      opacity: 0.3;
      transition: $tr_dur;

      &:hover {
        opacity: 1;
      }
    }
  }

  &__arrow--prev {
    left: 0;
  }

  &__arrow--next {
    right: 0;
  } 

  &__svg--prev,
  &__svg--next {
    stroke: $white;
    height: $arrow_height/2;
    width: $arrow_width/3;
    transform-origin: 0% 50%;
  }
}

.-next-disabled .arrows__arrow--next,
.-prev-disabled .arrows__arrow--prev,
.-first-page .arrows__arrow--prev,
.-navigation-locked .arrows__arrow--next,
.-navigation-locked .arrows__arrow--prev {
  opacity: 0.3;
  cursor: default;
}

.-next-disabled .arrows__svg--next,
.-prev-disabled .arrows__svg--prev,
.-first-page .arrows__svg--prev,
.-navigation-locked .arrows__svg--next,
.-navigation-locked .arrows__svg--prev {
  // stroke: #000;
}

