.page#explore .articles {
  h1 {
    background-color: $course_white;
    color: $course_black;
    margin: 1rem 0;
    padding: 1rem;
    border-radius: 5px;
  }
  article {
    background-color: $course_white;
    color: $course_black;
    margin: 1rem 0;
    padding: 1rem;
    border-radius: 5px;
    &:first-of-type {
      margin-top: 0;
    }
  }
  a, a:hover {
    color: $course_black;
  }
}