// ====================================
// Main Page Content
// ====================================
.pages {
  @include clearfix;
  width: 100vw;
  z-index: 0;
}

.page--psuedo {
  content: '';
  display: block;
  height: 1px;
  width: 100%;
  float: left;
  visibility: hidden;
  margin: 0;
}

.page, .page--start {
  display: none;
  width: 100vw;
  z-index: 1;
  float: left;
  margin-left: -100%;
  position: relative;
  height: 100%;
  min-height: 100vh;
  width: 100vw;
  background-image: $background_gradient;
  color: $course_white;

  &:first-child {
    margin-left: 0;
  }

  &__flex-wrapper-1 {
    display: flex;
    flex-direction: column;
    height: 100%;
    min-height: 100vh;
    position: relative;
  }

  &__flex-wrapper-2 {
    display: flex;
    align-items: center;
    min-height: 100vh;
  }

  &__content {
    max-width: 52rem;
    min-height: 40vh;
    width: 100%;
    margin: 0 auto;
    padding: 0 2rem;
    padding-top: $menu_height + 2 * $menu_padding;
    padding-bottom: $arrow_height;
    border-radius: $border_radius;
    @media #{$desktop} {
      width: calc( 100% - #{ $arrow_width * 2 });
      padding-bottom: $footer_height;
    }
  }
}

.child-page {
  visibility: hidden;
}

.full-width-container {
  right: 0;
  position: absolute;
  left: 0;
}
