.page#explore {
  .page__flex-wrapper-2 {
    align-items: start;
  }
  .explore-tabs {
    background-image: linear-gradient(102deg, #6578c5, #4049b2);
    display: flex;
    height: 2rem;
    font-weight: bold;
    letter-spacing: 0.2px;
    line-height: 2rem;
  }
  a.tab {
    color: $course_white;
    display: inline-block;
    flex: 1;
    text-align: center;
    &.active {
      background-color: #fff;
      color: $deep_blue;
    }
  }
  .tab-content {
    display: none;
  }
}