.form {
  $primary : $course_primary;
  $highlight: rgba($course_primary, 0.2);
  $highlight_half: rgba($course_primary, 0.1);
  $error: $course_bad;
  $highlight_error: rgba($course_bad, 0.2);
  
  &__label {
    display: block;
    padding: leading() 2em;
    margin: 0 -2em;
    border-top : 1px solid transparent;
    border-width: 1px 0;
    transition: background-color 0.3s linear;

    &:hover {
      background-color: $highlight_half;      
      transition: background-color 0.15s linear;
    }

    &.-is-focused {
      background-color: $highlight;
      transition: background-color 0.15s linear;
    }

    &.-has-error {
      background-color: $highlight-error;
      border-color: $error;

      .form__validation-error {
        visibility: visible;
      }

      .form__input-wrapper {
        color: $error;

        &:before {   
          border-color: $error;     
          transform: scaleX(1);
          opacity: 1;
          transition: transform 200ms ease-out, opacity 500ms step-start, border-color 100ms step-end;
        }
      }
    }
  }

  &__input,
  &__textarea {
    width: 100%;
    outline: none!important;
    padding: leading(0.5);
    line-height: leading(1.5);
    font: inherit;
    border: 1px solid rgba(#000,.2) !important;
    display: block;
    color: $course_black;
  }

  &__input-wrapper {
    position: relative;

    &:before {
      content: '';
      display: block;
      width: 100%;
      position: absolute;
      top: 0;      
      left: 0;
      border-bottom: 0.2em solid $primary;
      transform: scaleX(0);
      transform-origin: 0 0;
      opacity: 0;
      transition: transform 300ms step-end, opacity 200ms linear, border-color 100ms step-end;
    }

    &.-is-focused:before {
      transform: scaleX(1);
      opacity: 1;
      transition: transform 300ms ease-out, opacity 500ms step-start, border-color 100ms step-end;
    }
  }

  &__validation-error {
    font-size: 0.8em;
    visibility: hidden;
  }


  &__input-name {
    display: flex;
  }

  &__input-name-label {
    flex: 8;
  }

  &__input-name-label:first-child {
    flex: 5;
    margin-right: 1em;
  }

  &__input-name-label p {
    margin: 0 0 1em;
  }

  &__radio-label,
  &__checkbox-label {
    display: block;
    padding: leading(0.5) 0.5em;

    &:hover {
      background-color: $highlight;
    }
  }

  &__radio-input,
  &__checkbox-input  {
    margin-right: 1ch;
  }

  &__textarea {
    height: leading(6);
  }

  select,
  option {
    padding: leading(0.5);
    line-height: leading();
  }
}
