// ====================================
//  marketing
// ====================================

.marketing_buttons {
  position: absolute;
  right: leading(0.5);
  top: leading(0.5);

  &__button {
    @include button;
    cursor: pointer;
  }
}


.marketing_lightboxes .lightbox {
	&__box {
	}
	&__content {
		overflow-y: hidden;
		max-height: 550px;
		font-size: 14px;
	}
	@media (max-width: 30em) {
		& .pricing-description{
			display: none;
			}
	}
}

.sign_up__main-menu__button {
	display: none;
}
@media #{$desktop} {
 	.sign_up__main-menu__button {
		display: initial;
	}
}

#sign_up {
	& .hide {
		display: none;
	}
	width: 60%;
  @media #{$phone} {
  	width: 90%;
  }
  @include absolute_centered;
  left: 50vw;
  // background: transparent linear-gradient($course_primary, $course_primary leading(1.5), $course_white leading(1.5), $course_white);
  padding: 0 leading(.5);
  box-shadow: 0px 6px 30px 7px rgba($course_black, .8);
  border-radius: $border_radius;
  max-height: 90vh;
  min-width: 60%;
  overflow-y: auto;
  color: #051a26;
  font-size: 18px;
  background: $course_white;
	border-top: leading(1.5) $course_primary solid;
  h2 {
    color: #335099;
    font-size: 32px;
    text-align: center;
    margin-left: 1%;
    margin-right: 1%;
  }
  p {
    color: #051a26;
    font-size: 18px;
    margin-left: 1%;
    margin-right: 1%;
  }
  input {
    font-size: 18px;
    width: 100%;
  }
	.button-container {
		max-width: 40rem;
		margin: 2rem auto 0;
	}
  ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    li {
      @include clearfix;
      font-size: 16px;
      margin: 0;
      margin-bottom: 20px;
      padding: 6px 1% 9px 1%;
      clear: both;
      text-align: left;
      background-color: transparent;
      position: relative;
      -webkit-transition: background-color 350ms ease-out;
      transition: background-color 350ms ease-out;
      &.focused {
        background-color: #fff7c0;
      }
      & .field {
      	border: 1px solid rgba(0, 0, 0, 0.2) !important;
      	line-height: 1.4;
      	padding: 5px;
      }
      @media (min-width: 900px) {
        &.leftHalf, &.rightHalf {
        width: 47%;
      }
      &.leftHalf {
        // clear: none;
        float: left;
      }
      &.rightHalf {
        clear: none;
        float: right;
      }
      }
      .instruct {
        visibility: visible;
        position: static;
        margin: 0;
        padding: 6px 0 0 0;
        width: 100%;
        clear: left;
        background: none !important;
        border: none !important;
        font-style: italic;
        opacity: 1;
      }
      label.desc {
        font-size: 18px;
        font-weight: 500;
        line-height: 150%;
        margin: 0;
        padding: 0 0 3px 0;
        border: none;
        display: block;
        width: 100%;
      }
      span {
        float: left;
        margin: 0 .3em 0 0;
        width: 46%;
        &.req {
          display: inline;
          float: none;
          color: red !important;
          font-weight: bold;
          margin: 0;
          padding: 0;
        }
      }
    }
  }
  select {
    color: black;
    padding: 3px;
    width: 91%;
    font-size: 18px;
  }

  .-has-close-button & {
    margin: leading(1) 0;
  }

  input.parsley-success,
  select.parsley-success,
  textarea.parsley-success {
  color: #468847;
  background-color: #DFF0D8;
  border: 1px solid #D6E9C6;
  }

  input.parsley-error,
  select.parsley-error,
  textarea.parsley-error {
  color: #B94A48;
  background-color: #e18f8f;
  border: 1px solid #EED3D7;
  }

  .parsley-errors-list {
  margin: 2px 0 3px;
  padding: 0;
  list-style-type: none;
  font-size: 0.9em;
  line-height: 0.9em;
  opacity: 0;

  transition: all .3s ease-in;
  -o-transition: all .3s ease-in;
  -moz-transition: all .3s ease-in;
  -webkit-transition: all .3s ease-in;
  }

  .parsley-errors-list.filled {
  opacity: 1;
  }


  &__heading {
    color: $course_primary;
    text-align: center;
  }

  &__buttons {
    @extend %buttons;
  }

  %modal__button {
    @include button;
    font-weight: 700;
    letter-spacing: 0.05rem;
  }

  &__button {
    @extend %modal__button;
  }

  &__button--next {
    @extend %modal__button;
  }

  &__button--cancel {
    @extend %modal__button;
  }
}
