.main-menu {
  z-index: 0;
  width: 100%;

  &__button {
    z-index: 2;
    background: rgba($course_white, 0.3);
    // background: url('../img/course_icon.svg') 0 0 / contain;
    border-radius: 50%;
    position: absolute;
    top: $menu_padding;
    left: $menu_padding;
  }

  &__button-hint {
    @include peripheral_hint;
    left: $menu_height + 1rem;
    z-index: 1;

    .-show-hints & {
      @include visible;
    }

    .-open-menu & {
      display: none!important;
    }
  }

  &__mute {
    position: fixed;
    top: $menu_padding*2 +$menu_height;
    left: $menu_padding + leading(0.5) + 0.2rem;
    width: leading();
    height: leading();

    .-audio-disabled & {
      display: none;
    }
  }

  &__mute-button {    
    position: absolute;
    width: leading();
    height: leading();
    background: transparent url('../img/unmute.svg') no-repeat;
    background-size: contain;
    background-position: 50% 50%;
    opacity: 0.6;
    transition: opacity 0.4s;

    &:hover {
      opacity: 1;
    }

    &.-is-muted {
      background-image: url('../img/mute.svg');
    }
  }

  &__mute-hint {
    @include peripheral_hint;
    left: leading(1) + 1rem;
    z-index: -1;
    pointer-events: none;

    .-show-hints & {
      @include visible;
    }
  }

  &__close-button {
    color: $course_black;
    font: 0.6rem/1 sans-serif;
    cursor: default;

    &:before {
      @include border_radius;
      content: 'âœ–';
      position: absolute;
      z-index: 2;
      opacity: 0.7;
      transition: $tr-dur;
      margin: $menu_padding/2;
      top: 0;
      right: 0;
      padding: 0.5em;
      background-color: transparent;
    }

    &:hover:before {
      opacity: 1;
      background-color: rgba($course_black, 0.1);
    }
  }

  &__close-overlay {
    @include fill_window;
    z-index: -1;
    display: none;
    background: rgba($course_black, 0.5);
  }

  &__content {
    z-index: 1;
    position: absolute;
    width: 95%;
    max-width: 95%;

    @media (min-width: 20em) {
      width: auto;
      min-width: 20rem;
      max-width: calc(100vw - 3rem);
    }

    background-color: darken($course_white, 10%);
    top: 0;
    padding-top: $menu_padding*2 +$menu_height;
    transform: translateX(-100%);
    transition: $tr_dur;
    border-radius: 0 0 $border_radius 0;
  }

  &__progress {
    @media (max-width: 20em) {
      display: none;
    }

    height: 3px;
    width: 14.2rem;
    margin-left: $menu_height + $menu_padding * 2.5 + 0.1rem;
    position: absolute;
    top: leading(2);
    background: rgba($course_primary, 0.5);
    
    &-fill {
      content: '';
      display: block;
      @include fill_parent;
      background: $course_black;
      transform: scaleX(0);
      transform-origin: 0 0;
      transition: $tr_dur;
    }
  }

  &__heading {
    margin: 0;
    margin-left: $menu_height + $menu_padding * 2.5;
    position: absolute;
    top: 1rem;
    font-size: 1.1rem;
    font-weight: 400;
    line-height: leading(1);
    color: $course_primary;
    cursor: pointer;

    @media (max-width: 20em) {
      position: relative;
      margin: leading(0.5) 1rem;
      top: 0;
      font-size: 1.3rem;
    }
  }

  &__list {
    background-color: $course_white;
    max-height: calc(95vh - #{$menu_padding * 2 + $menu_height});
    overflow: auto;
    margin-bottom: leading(0.5);
  }

  &__numbering-2 {
    margin-right: 0.5ch;
  }

  &__numbering-1 {
    font-weight: 700;
    width: 3rem;
    display: inline-block;
    float: left;
    text-align: right;
    padding-right: 1ch;
    flex: none;
  }

  &__text-level-1 {
    flex: 1;
  }

  &__item {
    $padding_top: (leading() - 0.9rem)/2;

    display: block;
    visibility: hidden;
    padding: $padding_top 1rem $padding_top 3rem;
    font-size: 0.8rem;
    line-height: 0.9rem;
    color: $course_black;
    transition: 150ms;

    &[onclick] {
      cursor: pointer;
      
      &:hover {
        color: $course_white;
        background-color: $course_primary;
        transition: 100ms;
      }
    }

  }

  &__item--locked,
  &__item--locked:hover {
    opacity: 0.4;
    cursor: default!important;
    color: $course_black!important;
    background-color: $course_white;
  }

  &__item--level-2 {
    font-size: 1.2rem;
    line-height: 1.25;
    padding: leading(0.5) 2rem leading(0.5) 1rem;
    background-color: darken($course_white, 4%);
  }

  &__item--level-1 {
    display: flex;
    font-size: 1rem;
    line-height: 1.25;
    padding: leading(0.5) 1rem leading(0.5) 0;
  }

  &__item--current {
    background-color: $course_primary;
    color: $white;
    cursor: default;
    position: relative;

    &:before {
      content: 'â˜…';
      position: absolute;
      left: 2rem;
    }
  }

  &__item--level-2.main-menu__item--current {
    padding-left: 2.4rem;

    &:before {
      left: 1rem;
    }
  }
}

.-open-menu {
  overflow: hidden;

  .main-menu {
    &__button {
      box-shadow: none!important;
      cursor: initial;
    }

    &__content {
      transform: translateX(0);
    }

    &__close-overlay {
      display: block;
    }

    &__progress-fill {
      transform: scaleX(1);
      transition: $tr_dur*3 ease-out;
    }

    &__item,
    &__item--current,
    &__item--locked {
      visibility: visible;
    }
  }
}

.-first-page .main-menu__heading {
  cursor: default;
}
