.gate {
  @include fill_window;
  z-index: 3;
  visibility: hidden;
  opacity: 0;
  transform: translateX(3em);
  transition: opacity $tr_dur, transform $tr_dur step-end, visibility 0s $tr_dur;

  .-show-gate & {
    visibility: visible;
    opacity: 1;
    transition: opacity $tr_dur, transform $tr_dur, visibility 0s 0s;
    transform: translateX(0);
  }

  &__box {
    width: 20em;
    max-width: calc(100% - #{2*$arrow_width + leading(2)});
    background: $course_white;
    bottom: leading();
    position: absolute;

    @media #{$desktop} {
      bottom: auto;
      @include absolute_centered_Y;
    }

    right: $arrow_width + leading();
    padding: leading();
    @include border_radius;
    box-shadow: 0 15px 40px 5px rgba($course_black, 0.3);
  }


  &__text{
    padding-left: 4rem;
    margin-left: -0.5rem;
    background: url('../img/alert.svg') no-repeat 0 0rem / #{leading(2)} #{leading(2)};
    min-height: leading(2);
  }

  &__button {
    @include button;
    display: block;
    width: 0;
    margin: leading() auto 0;
  }
}
