// ====================================
// Base Styles
// ====================================

* {
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  font-size: $base_font_size;
  font-family: $font_family_primary;
  line-height: $leading;
  color: $course_black;
  background: $course_black;
  overflow-x: hidden;
  overflow-y: auto;

  @media (max-width:400px) {
    font-size: $base_font_size*.8;
  }
  @media (min-width:2000px) {
    font-size: 2vh;
  }
}

body {
  position: relative;
  overflow-y: auto;
}

@media print {
  * {
    color: #000!important;
    background: transparent!important;
  }
}

// Lists

ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
}


// Button

button {
  outline: 0;
}

.page {
  background: $course_primary;
}

code {
  background: rgba($course_black, 0.2);
  border-radius: $border-radius;
  padding: 0 0.5ch;
} 
