.annotation {
  position: relative;
  z-index: 0;
  margin-right: 0.5ch;

  &__body {
    background: rgba($course_black, 0.25);
    color: $course_white;
    padding: 0.1ch 0.6ch;
    box-decoration-break: clone;
    @include border_radius;
    z-index: 1;

    // allows background to clone in IE11
    position: relative;

    &:after {
      $width: 12px;

      font: $width sans-serif;
      content: '+';
      display: inline-block;
      background: $course_black;
      // background: $course_black url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 11 11" width="11" height="11"><path shape-rendering="auto" style="stroke:#fff;stroke-width:1;" d="M 5.5,2 v 7 M 2,5.5 h 7"></path></svg>');
      color: $course_white;
      position: absolute;
      top: -$width/2;
      right: -$width/2;
      padding: $width/2 0;
      height: 0;
      width: $width;
      line-height: 0;
      text-align: center;
      border-radius: 50%;
      transition: $tr_dur cubic-bezier(0,0,0.5,1.8);
    }
  }

  &__note {
    @include hidden;
    color: $course_white;
    text-align: left;
    transition: opacity $tr_dur, transform $tr_dur step-end, visibility $tr_dur step-end;
    @include border_radius;
    transform: translateX(50%) translateY(13px) scale(0);
    transform-origin: 50% 100%;
    display: inline-block;
    font-size: 0.85em;
    line-height: 1.3;
    width: 200%;
    max-width: 20em;
    hyphens: manual;
    position: absolute;
    bottom: leading();
    right: 0;
    padding: leading(0.5) 1rem;
    background-color: $course_black;
    z-index: -1;

    // fill the gap with transparent psuedo
    &:after {
      content: '';
      display: block;
      position: absolute;
      height: 0.3rem;
      width: 100%;
      left: 0;
      bottom: -0.3rem;
      background-color: transparent;
    }
  }

  &__asterisk {
    // hidden span with full height and 0 width
    visibility: hidden;
    overflow: hidden;
    text-indent: -1em;
    position: absolute;
  }

  &:hover {
    .annotation__note {
      @include visible;
      transition: opacity $tr_dur step-start, transform $tr_dur ease-out, visibility $tr_dur step-start;
      transform: translateX(50%) translateY(0) scale(1);
    }
    .annotation__body:after {
      transform: scale(0);
      transition: $tr_dur;
    }
  }

  &.-near-right-edge .annotation__note {
    bottom: leading();
    right: 0;
    left: auto;
    margin-right: 100%;
    transform-origin: 100% 100%;
    padding: leading(0.5) 1rem;
    background-color: $course_black;
    z-index: -1;
  }

  &.-has-line-break .annotation__note {
    bottom: leading(2);
    width: 20em;
    right: 0;
    left: auto;
    margin-right: 100%;
    transform-origin: 100% 100%;
    padding: leading(0.5) 1rem;
    background-color: $course_black;
    z-index: -1;
  }
}
