// ====================================
//   Lightboxes
// ====================================

.lightbox {
  @include fill_window;
  @include flex_parent_centered;
  z-index: 5;
  display: none;

  &__overlay {
    @include fill_window;
    background: rgba(#000, 0.3);
  }

  &__box {
    width: 90%;

    @media (min-width: 40em){
      max-width: 40em;

      &--wide {
       min-width: 60%;
      }
    }

    @media (max-width: 35em) {
      width: 90%;
    }
    
    background: transparent linear-gradient($course_primary, $course_primary leading(1)/4, $course_white leading(1)/4, $course_white);
    padding: leading(1.5) leading(.5);
    box-shadow: 0px 6px 30px 7px rgba($course_black, .8);
    border-radius: $border_radius;
  }

  &__close-button {
    @include flex_parent_centered;
    @include invisible_text;
    display: none;
    position: absolute;
    top: leading(.5);
    right: leading(.5);
    border: 0;
    width: leading(1);
    height: leading(1.5);
    z-index: 1;
    transition: $tr-dur;
    border-radius: $border_radius;
    background-color: transparent;
    padding: 1rem;
    margin: -0.375rem;
    opacity: 0.5;

    .-has-close-button & {
      display: flex;
    }
    
    &:after {
      content: 'âœ–';
      color: $course_primary;
      font: 1rem/1 sans-serif;
      transition: $tr-dur;
    }

    &:hover{
      opacity: 1;
    }
  }

  &__content {
    max-height: 50vh;
    overflow-y: auto;
    padding: 0 1.5rem;
    margin: leading(1) 0;
  }

  &__heading {
    color: $course_primary;
    text-align: center;
  }

  &__buttons {
    @extend %buttons;
  }

  %lightbox__button {
    @extend %button;
    font-weight: 700;
    letter-spacing: 0.05rem;    
  }

  &__button {
    @extend %lightbox__button;
  }

  &__button--next {
    @extend %lightbox__button;
  }

  &__button--cancel {
    @extend %lightbox__button;
  }

  &__button--close {
    @extend %lightbox__button;
  }
}


body.-is-overlaid {
  overflow: hidden;

  .header, 
  .page, 
  .footer {
  }

}

